@import url(https://fonts.googleapis.com/css?family=Manjari);
@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box; 
}
* {
  box-sizing: border-box; }

.App {
  font-family: 'Raleway';
  text-align: center;
  text-transform: capitalize;
  color: #FF9770;
}

.container {
  display: flex;
  opacity: 0.9;
  background-color: whitesmoke;
  justify-content: center;
}

.logo {
  display: block;
  margin: 20px auto;
  max-height: 80px;
}

form {
  padding: 1% 0;
  background: pink;
}

h1 {
  font-family: 'Manjari';
  font-size: 45px;
}

img {
  height: 16em;
}

input {
  color: #FF9770;
  box-sizing: border-box;
  font-size: 1.5rem;
  padding: 1rem;
  display: block;
  margin: 2rem auto;
  border: 3px solid pink;
  border-radius: 10px;
  font-family: 'Manjari';
}


.pokeName {
  color: white;
  margin: 1em;
} 

.pokeTable {
  color: #9475BD;
  padding: 2% 0;
  display: table;
  max-width: 20em;
  float: right;
  display: flex;
  align-items: center;
}

.pokeTableRow {
  background-color: #EAE3F2;
  display: table-row;
}

.pokeTableHeading {
  display: table-header-group;
}

.pokeTableCell,
.pokeTableHead {
  text-align: left;
  border: 2px solid pink;
  display: table-cell;
  padding: 3px 10px;
  font-size:16px;
  font-weight: 800;
}

.pokeTableHeading {
  display: table-header-group;
  font-weight: bold;
}

.pokeTableFoot {
  display: table-footer-group;
  font-weight: bold;
}

.pokeTableBody {
  border-radius: 5px;
  border: 2px solid pink;
  display: table-row-group;
}

.pokeTableCell:nth-child(odd) {
  background-color: #EAE3F2;
}

.pokeStat-bar {
  text-align: center;
  background: pink;
  border-radius: 10px;
  color: white;
}

.pokeWeight {
  text-transform: lowercase;
}


